<template>
    <div>
        <v-banner
                elevation="0"
                outlined
                rounded
                class="mr-5 ml-5 mt-3"
                color="white"
        >
            <v-row>
                <v-card-subtitle class="text-h5" style="color: grey">
                    Filter By:
                </v-card-subtitle>
                <v-card-title v-for="filter in filters" :key="filter.id" class="text-h5 text-md-h3">
                    {{filter.name}}

                    <v-btn
                            icon
                            @click="show(filter)"
                    >
                        <v-icon>{{ filter.show ? 'mdi-chevron-down' : 'mdi-chevron-up' }}</v-icon>
                    </v-btn>
                </v-card-title>
                <v-card-title class="text-h5 ml-md-auto mr-5">
                    <v-btn color="primary">
                        Clear all filters
                    </v-btn>
                </v-card-title>
            </v-row>
            <v-expand-transition v-for="filter in filters" :key="filter.id">

                <div v-show="filter.show">
                    <v-divider></v-divider>

                    <v-card-text>
                        {{filter.text}}
                    </v-card-text>
                </div>


            </v-expand-transition>
        </v-banner>
        <v-row class="mt-0 mb-0 ml-3 mr-3" style="background-color: transparent">
            <v-col v-for="item in list"
                   :key="item.id"
                   cols="12"
                   lg="3"
                   md="4"
                   sm="6"
            >

                <item-card :item="item" @add-me="addToShoppingCart(item)"></item-card>
            </v-col>
        </v-row>
    </div>

</template>

<script>
    import ItemCard from './item-card';

    export default {
        name: "item-page",

        props: {
            list: {
                type: Array
            },


        },

        data() {
            return {
                filters: [
                    {
                        name: 'Features',
                        text: 'features',
                        show: false,
                    },
                    {
                        name: 'Variety',
                        text: 'variety',
                        show: false,
                    },
                    {
                        name: 'Size',
                        text: 'size',
                        show: false,
                    }

                ],

                featureShow: false,
            }
        },

        components: {
            ItemCard
        },
        methods: {
            addToShoppingCart: function (item) {
                this.$emit('add-me', item);
            },

            show: function (filter) {
                for (var item in this.filters) {
                    if (this.filters[item] != filter) {
                        this.filters[item].show = false;
                    }
                    filter.show = !filter.show;
                }
            }
        },
    }
</script>

