<template>
    <v-card
            elevation="0"
            class=""
            hover
            max-width="700"
    >
        <v-img
                :src="item.image"
                min-height="450px"
        ></v-img>
                <v-card-title class="pt-1" style="word-break: break-word; min-height: 96px">
                    <div style="width: 70%">
                        {{item.title}}
                    </div>


                    <v-spacer></v-spacer>

                    <div class="mr-2">
                        {{item.qty}}
                    </div>

                    <v-btn
                            class="mr-2 text-right"
                            fab
                            dark
                            small
                            color="primary"
                            @click="addToShoppingCart()"
                    >
                        <v-icon >
                            add_shopping_cart
                        </v-icon>
                    </v-btn>
                </v-card-title>

                <v-card-subtitle>
                    Price: {{'$' + item.price}}<br>

                </v-card-subtitle>
    </v-card>
</template>

<script>
    export default {
        name: "item-card",

        props: {
            item : Object,
        },

        components: {

        },

        data: () => ({
            show: false,
        }),


        methods: {
            addToShoppingCart : function(){
                this.$emit('add-me', this.item);
            }
        },
    }
</script>

