import {IndoorPlant, OutdoorPlant} from "@/models/Plants";
import Pot from "@/models/Pots";

function PlantFactory(){

    this.create = function(item){

        if(item.category === 'indoor'){
            return new IndoorPlant(item.title || null, item.description || null,
                item.image || null, item.category || null,
                item.light || null, item.soil || null,
                item.difficulty || null, item.petFriendly || null,
                item.size || null);
        }
        else if(item.category ==='outdoor'){
            return new OutdoorPlant(item.title || null,
                item.description || null, item.image || null)
        }
        else if(item.category ==='cactiSucculent'){
            return new IndoorPlant(item.title || null, item.description || null,
                item.image || null, item.category || null,
                item.light || null, item.soil || null,
                item.difficulty || null, item.petFriendly || null,
                item.size || null)
        }
        else if(item.category ==='pot'){
            return new Pot(item.title || null, item.description || null,
                item.image || null, item.drainage || null,
                item.size || null)
        }
    }
}

export default PlantFactory;
